/* stylelint-disable */
/* stylelint-enable */
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: 50%;
}
@media only screen and (min-width: 56.25em) {
  html {
    font-size: 56.25%;
  }
}
@media only screen and (min-width: 63.125em) {
  html {
    font-size: 62.5%;
  }
}
@media only screen and (min-width: 75.0625em) {
  html {
    font-size: 75%;
  }
}

body {
  font-family: "Nunito", sans-serif;
  color: #6d5d4b;
  font-weight: 300;
  line-height: 1.6;
}

::selection {
  background-color: #54483a;
  color: #f9f7f6;
}

/* stylelint-disable */
/* stylelint-enable */
.heading-1,
.heading-2,
.heading-3,
.heading-4 {
  font-family: "Josefin Sans", sans-serif;
  font-weight: 400;
}

.heading-1--light,
.heading-2--light,
.heading-3--light,
.heading-4--light {
  color: #f9f7f6;
}
.heading-1--dark,
.heading-2--dark,
.heading-3--dark,
.heading-4--dark {
  color: #54483a;
}

.heading-1 {
  font-size: 4.5rem;
  color: #f9f7f6;
  line-height: 1;
  margin: 0;
}

.heading-2 {
  font-size: 4rem;
  font-style: italic;
  line-height: 1;
}

.heading-3 {
  font-size: 1.6rem;
  text-transform: uppercase;
  color: #c69963;
}

.heading-4 {
  font-size: 1.9rem;
}

/* stylelint-disable */
/* stylelint-enable */
.mb-sm {
  margin-bottom: 2rem;
}

.mb-md {
  margin-bottom: 3rem;
}

.mb-lg {
  margin-bottom: 4rem;
}

.mb-hg {
  margin-bottom: 8rem;
